import React from 'react';
import clsx from 'clsx';
import { createStyles, makeStyles } from '@material-ui/core';
import { darkMain, lightMain } from 'src/theme';

type Props = {
  transitioningTo: 'dark' | 'light' | undefined;
};

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      root: {
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        zIndex: 99999999,
      },

      svg: {
        width: '100%',
        height: '100%',
      },

      dark: {
        fill: darkMain,
      },

      light: {
        fill: lightMain,
      },
    }),
  { name: 'ThemeSwitcher' },
);

export function ThemeSwitcher({ transitioningTo }: Props) {
  const classes = useStyles();

  if (!transitioningTo) {
    return null;
  }

  const animate = { dur: '0.8s', repeatCount: '1' };

  const ellipseCoords =
    transitioningTo === 'light'
      ? { cx: '100', cy: '0' }
      : { cx: '0', cy: '100' };

  return (
    <div className={classes.root}>
      <svg
        viewBox="0 0 100 100"
        preserveAspectRatio="none"
        className={clsx(classes.svg, classes[transitioningTo])}
      >
        <ellipse rx="0" ry="0" {...ellipseCoords}>
          <animate attributeName="rx" values="14; 150" {...animate} />
          <animate attributeName="ry" values="29; 150" {...animate} />
        </ellipse>
      </svg>
    </div>
  );
}
