import React, { useEffect, useMemo, useReducer, useState } from 'react';
import { Redirect, Route, Switch, useLocation } from 'wouter';
import {
  ThemeProvider,
  createMuiTheme,
  CssBaseline,
  Container,
} from '@material-ui/core';
import { darkPalette, lightPalette, baseTheme } from './theme';
import { Footer, Nav, ThemeSwitcher } from './components';
import { About, Home, NotFound, Project } from './pages';
import routes, { ParamsOf } from './routes';
// import { Cursor } from './components/Cursor';

type ThemeState = {
  theme: 'dark' | 'light';
  transitioningTo?: 'dark' | 'light';
};

type ThemeAction =
  | { kind: 'SetTheme'; theme: ThemeState['theme'] }
  | { kind: 'SetTransitioning'; to: ThemeState['transitioningTo'] };

const themeReducer = (state: ThemeState, action: ThemeAction): ThemeState => {
  switch (action.kind) {
    case 'SetTheme':
      return { ...state, theme: action.theme, transitioningTo: undefined };

    case 'SetTransitioning':
      return { ...state, transitioningTo: action.to };
  }
};

const initialThemeState: ThemeState = {
  theme: 'dark',
};

function App() {
  const [themeState, dispatchTheme] = useReducer(
    themeReducer,
    initialThemeState,
  );

  const [displayContent, setDisplayContent] = useState(true);

  const [menuOpen, setMenuOpen] = useState(false);

  const [location] = useLocation();

  const toggleTheme = () => {
    if (themeState.transitioningTo) {
      return;
    }

    const nextTheme = themeState.theme === 'dark' ? 'light' : 'dark';

    dispatchTheme({ kind: 'SetTransitioning', to: nextTheme });

    setTimeout(() => {
      setDisplayContent(false);
      dispatchTheme({ kind: 'SetTheme', theme: nextTheme });
      setDisplayContent(true);
    }, 750);
  };

  const toggleMenu = () => {
    setMenuOpen((open) => !open);
  };

  useEffect(() => {
    window.scrollTo(0, 0);
    setMenuOpen(false);
  }, [location]);

  const muiTheme = useMemo(
    () =>
      createMuiTheme({
        ...baseTheme,
        palette: {
          ...baseTheme.palette,
          ...(themeState.theme === 'dark' ? darkPalette : lightPalette),
        },
      }),
    [themeState.theme],
  );

  return (
    <ThemeProvider theme={muiTheme}>
      <CssBaseline />

      {/* <Cursor /> */}
      <ThemeSwitcher transitioningTo={themeState.transitioningTo} />

      {displayContent && (
        <Container maxWidth="lg">
          <Nav
            theme={themeState.theme}
            onToggleTheme={toggleTheme}
            menuOpen={menuOpen}
            onToggleMenuOpen={toggleMenu}
          />

          <Switch>
            <Route path={routes.Home.template}>
              <Home />
            </Route>

            <Route path={routes.About.template}>
              <About />
            </Route>

            <Route path={routes.Projects.template}>
              <Redirect to={routes.Home.build()} />
            </Route>

            <Route<ParamsOf<typeof routes.Project>>
              path={routes.Project.template}
            >
              {(params) => <Project projectName={params.name} />}
            </Route>

            <Route>
              <NotFound />
            </Route>
          </Switch>

          <Footer />
        </Container>
      )}
    </ThemeProvider>
  );
}

export default App;
