import React, { useCallback } from 'react';
import {
  createStyles,
  IconButton,
  makeStyles,
  SvgIcon,
  useScrollTrigger,
} from '@material-ui/core';
import clsx from 'clsx';

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      container: {
        position: 'fixed',
        right: theme.spacing(3),
        bottom: theme.spacing(3),
        transition: 'opacity .5s',
        opacity: 0,
        visibility: 'hidden',

        backgroundColor: `${theme.palette.background.default}80`,
        backdropFilter: 'blur(5px)',

        [theme.breakpoints.up('sm')]: {
          right: theme.spacing(6),
          bottom: theme.spacing(6),
        },
      },

      visible: {
        opacity: 1,
        visibility: 'unset',
      },
    }),
  { name: 'ScrollTopButton' },
);

export function ScrollTopButton() {
  const classes = useStyles();

  const scrollToTop = useCallback(() => {
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'smooth',
    });
  }, []);

  const visibleTrigger = useScrollTrigger({
    disableHysteresis: true,
    threshold: 200,
  });

  return (
    <IconButton
      onClick={scrollToTop}
      className={clsx(classes.container, {
        [classes.visible]: visibleTrigger,
      })}
    >
      <SvgIcon viewBox="0 0 39 22">
        <path d="M38.0357 20.4297L36.8398 21.6838L19.5003 3.40884L2.16068 21.6838L0.964844 20.4297L19.5003 0.990085L38.0357 20.4297Z" />
      </SvgIcon>
    </IconButton>
  );
}
