import React from 'react';
import useInView from 'react-cool-inview';
import clsx from 'clsx';
import {
  createStyles,
  Grid,
  makeStyles,
  Typography,
  Link as LinkComponent,
} from '@material-ui/core';
import { CountInfo, NoGutters, Link } from 'src/components';
import { Arrow } from 'src/icons';
import { ProjectSlug, Project } from 'src/projects';
import routes from 'src/routes';

type Props = {
  slug: ProjectSlug;
  title: string;
  category: Project<any>['category'];
  number: number;
  projectCount: number;
  imageSrc: string;
};

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      root: {
        opacity: 0,
        transform: 'translateX(-10px)',
        transition: `opacity .75s ease-out,
                     transform .75s ease-out`,

        '&$inView': {
          opacity: 1,
          transform: 'none',
        },
      },
      inView: {},

      container: {
        textDecoration: 'none',

        '&:hover $image': {
          transform: 'scale(1.02)',
        },

        '&:hover $header': {
          transform: 'translateY(-10px)',
        },
      },

      image: {
        width: '100%',
        marginTop: 5,
        marginBottom: 10,
        minHeight: 285,
        objectFit: 'cover',
        objectPosition: 'left',
        transition: 'transform .33s',
        transformOrigin: 'center bottom',
      },

      arrow: {
        [theme.breakpoints.down('xs')]: {
          display: 'none',
        },
      },

      header: {
        transition: 'transform .33s',
        color: theme.palette.text.primary,
      },
    }),
  { name: 'ProjectCard' },
);

const categoryText = (category: Project<any>['category']) => {
  switch (category) {
    case 'ui/ux':
      return 'UI/UX DESIGN';
    case 'graphic':
      return 'GRAPHIC DESIGN';
    case 'ux':
      return 'UX DESIGN';
  }
};

export function ProjectCard({
  slug,
  title,
  category,
  number,
  projectCount,
  imageSrc,
}: Props) {
  const classes = useStyles();

  const { ref, inView } = useInView<HTMLDivElement>({
    threshold: 0.1,
  });

  return (
    <div
      ref={ref}
      className={clsx(classes.root, {
        [classes.inView]: inView,
      })}
    >
      <Link
        href={routes.Project.build({ name: slug })}
        className={classes.container}
      >
        <Grid
          container
          justify="space-between"
          alignItems="center"
          className={classes.header}
        >
          <Grid item>
            <Typography variant="subtitle2">
              {categoryText(category)}
            </Typography>
          </Grid>
          <Grid item>
            <CountInfo number={number} projectCount={projectCount} />
          </Grid>
        </Grid>

        <NoGutters>
          <img src={imageSrc} alt="" className={classes.image} />
        </NoGutters>

        <Grid container justify="space-between" alignItems="center">
          <Grid item>
            <LinkComponent
              variant="h3"
              component="p"
              color="inherit"
              underline="always"
            >
              {title}
            </LinkComponent>
          </Grid>

          <Grid item>
            <Arrow className={classes.arrow} />
          </Grid>
        </Grid>
      </Link>
    </div>
  );
}
