import React from 'react';
import { createStyles, Grid, makeStyles } from '@material-ui/core';
import { HiddenOverflow, Paragraph, Solution } from 'src/components';

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      img: {
        width: '100%',

        '&:not(:first-child)': {
          marginTop: 100,
        },
      },
    }),
  { name: 'IndexStandard' },
);

const images = ['rating', 'about', 'forecast', 'services', 'contact'];

export function IndexStandard() {
  const classes = useStyles();

  const img = (name: string) => (
    <img src={require(`./images/${name}.png`)} alt="" className={classes.img} />
  );

  return (
    <div>
      <HiddenOverflow>
        <Grid container spacing={6}>
          <Grid item sm={4} component={Paragraph} title="Project">
            The Index Standard provides index ratings and forecasts that aim to
            make index and ETF screening simpler and easier.
          </Grid>

          <Grid item sm={4} component={Paragraph} title="Challenge">
            Create an UI/UX friendly design for a website. At the beggining I
            had only a raw text document with the content.
          </Grid>

          <Grid item sm={4} component={Paragraph} title="My role">
            UI/UX designer
          </Grid>
        </Grid>
      </HiddenOverflow>

      <Solution />

      <div>
        {images.map(img).map((e, i) => (
          <React.Fragment key={i}>{e}</React.Fragment>
        ))}
      </div>
    </div>
  );
}
