import React from 'react';
import clsx from 'clsx';
import {
  createStyles,
  makeStyles,
  Grid,
  Link as LinkComponent,
  Button,
  Container,
  Hidden,
  Collapse,
} from '@material-ui/core';
import { Link, useLocation } from 'wouter';
import routes from 'src/routes';
import { Moon, Sun } from './icons';
import { Burger } from '../Burger';

type Props = {
  theme: 'dark' | 'light';
  onToggleTheme: () => void;
  menuOpen: boolean;
  onToggleMenuOpen: () => void;
};

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      container: {
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        zIndex: 99999,
        paddingTop: 10,
        paddingBottom: 10,
        backgroundColor: `${theme.palette.background.default}80`,
        backdropFilter: 'blur(5px)',
        userSelect: 'none',
      },

      filler: {
        height: 120,
      },

      nameContainer: {
        [theme.breakpoints.down('sm')]: {
          display: 'flex',
          justifyContent: 'center',
        },
      },

      themeToggle: {
        borderRadius: 20,
        width: 40,
        height: 40,
        minWidth: 'unset',

        '& path': {
          fill: theme.palette.text.primary,
        },

        [theme.breakpoints.up('md')]: {
          background: theme.palette.type === 'dark' ? '#D8D1D10F' : '#FFFFFF',
          width: 120,
        },
      },

      inactiveThemeIcon: {
        opacity: 0.3,

        [theme.breakpoints.down('sm')]: {
          display: 'none',
        },
      },

      mobileMenu: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2) - 10,

        '& > *': {
          display: 'block',
          textAlign: 'center',
          paddingTop: theme.spacing(2),
          paddingBottom: theme.spacing(2),
        },
      },

      spacingFixK: {
        '&:after': {
          content: '"K"',
          letterSpacing: 0,
        },
      },

      spacingFixS: {
        '&:after': {
          content: '"S"',
          letterSpacing: 0,
        },
      },

      spacingFixT: {
        '&:after': {
          content: '"T"',
          letterSpacing: 0,
        },
      },
    }),
  { name: 'Nav' },
);

export function Nav({
  theme,
  onToggleTheme,
  menuOpen,
  onToggleMenuOpen,
}: Props) {
  const classes = useStyles();

  const projectsLink = (
    <NavLink
      label="PROJECT"
      path={routes.Home.build()}
      linkClass={classes.spacingFixS}
    />
  );
  const aboutLink = (
    <NavLink
      label="ABOU"
      path={routes.About.build()}
      linkClass={classes.spacingFixT}
    />
  );

  return (
    <>
      <div className={classes.filler} />

      <div className={classes.container}>
        <Grid
          container
          alignItems="center"
          component={Container}
          maxWidth="lg"
          justify="space-between"
        >
          <Grid item xs={1} component={Hidden} mdUp implementation="css">
            <Burger open={menuOpen} onOpenToggle={onToggleMenuOpen} />
          </Grid>

          <Grid item xs className={classes.nameContainer}>
            <Link href={routes.Home.build()}>
              <LinkComponent
                variant="subtitle1"
                color="textPrimary"
                className={classes.spacingFixK}
              >
                ILONA OSTAPCHU
              </LinkComponent>
            </Link>
          </Grid>

          <Grid item xs={1} md={8}>
            <Grid container justify="flex-end" alignItems="center" spacing={10}>
              {[projectsLink, aboutLink].map((l, i) => (
                <Grid
                  item
                  key={i}
                  component={Hidden}
                  smDown
                  implementation="css"
                >
                  {l}
                </Grid>
              ))}

              <Grid item sm={4} container justify="flex-end">
                <Button className={classes.themeToggle} onClick={onToggleTheme}>
                  <Grid container justify="space-evenly" alignItems="center">
                    <Grid
                      item
                      component={Moon}
                      className={clsx({
                        [classes.inactiveThemeIcon]: theme === 'light',
                      })}
                    />
                    <Grid
                      item
                      component={Sun}
                      className={clsx({
                        [classes.inactiveThemeIcon]: theme === 'dark',
                      })}
                    />
                  </Grid>
                </Button>
              </Grid>
            </Grid>
          </Grid>

          <Grid item xs={12} component={Hidden} mdUp implementation="css">
            <Collapse in={menuOpen}>
              <div className={classes.mobileMenu}>
                {projectsLink}
                {aboutLink}
              </div>
            </Collapse>
          </Grid>
        </Grid>
      </div>
    </>
  );
}
function NavLink({
  label,
  path,
  linkClass,
}: {
  label: string;
  path: string;
  linkClass: string;
}) {
  const [location] = useLocation();

  return (
    <Link href={path}>
      <LinkComponent
        variant="subtitle2"
        color={path === location ? 'textPrimary' : 'textSecondary'}
        className={linkClass}
      >
        {label}
      </LinkComponent>
    </Link>
  );
}
