type LiteralPart = string;

type ParamPart<T extends string> = {
  kind: 'param';
  name: T;
};

type RoutePart<T extends string> = LiteralPart | ParamPart<T>;

type ParamNames<T extends Array<RoutePart<any>>> = Extract<
  T[number],
  ParamPart<any>
>['name'];

type ParamsFrom<T extends Array<RoutePart<any>>> = ParamNames<T> extends never
  ? void
  : Record<ParamNames<T>, string>;

type Route<TParts extends Array<RoutePart<any>>> = {
  template: string;
  build: (params: ParamsFrom<TParts>) => string;
};

export type ParamsOf<T extends Route<any>> = T extends Route<infer U>
  ? ParamsFrom<U>
  : never;

const partTemplate = (part: RoutePart<any>) => {
  if (typeof part === 'string') {
    return part;
  }

  return `:${part.name}`;
};

const format = (parts: string[]) => `/${parts.join('/')}`;

const route = <TParts extends Array<RoutePart<any>>>(
  ...parts: TParts
): Route<TParts> => ({
  template: format(parts.map(partTemplate)),
  build: (params) =>
    format(
      parts.map((part) => {
        if (typeof part === 'string') {
          return part;
        }

        return (params as any)[part.name];
      }),
    ),
});

const param = <T extends string>(name: T) => ({ kind: 'param' as const, name });

const routes = {
  Home: route(),
  Projects: route('projects'),
  Project: route('projects', param('name')),
  About: route('about'),
};

export default routes;
