import React from 'react';
import { createStyles, IconButton, makeStyles } from '@material-ui/core';
import clsx from 'clsx';

type Props = {
  open: boolean;
  onOpenToggle: () => void;
};

const stickHeight = 3;
const stickSpacing = 4;
const stickWidth = stickHeight * 3 + stickSpacing * 2;

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      root: {
        padding: 15,
      },

      box: {
        position: 'relative',
        display: 'flex',
        alignItems: 'center',
        width: stickWidth,
        height: stickWidth,
      },

      stick: {
        '&, &::before, &::after': {
          position: 'absolute',
          width: stickWidth,
          height: stickHeight,
          backgroundColor: 'currentColor',
          borderRadius: stickHeight,

          transitionProperty: 'transform',
          transitionDuration: '0.22s',
          transitionTimingFunction: 'cubic-bezier(0.55, 0.055, 0.675, 0.19)',
        },

        '&::before, &::after': {
          content: '""',
          display: 'block',
        },

        '&::before': {
          top: -(stickSpacing + stickHeight),

          transition: `top 0.1s 0.25s ease-in,
                       opacity 0.1s ease-in`,
        },

        '&::after': {
          bottom: -(stickSpacing + stickHeight),
          transition: `bottom 0.1s 0.25s ease-in,
                       transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19)`,
        },
      },

      open: {
        '& $stick': {
          transform: 'rotate(225deg)',

          transitionDelay: '0.12s',
          transitionTimingFunction: 'cubic-bezier(0.215, 0.61, 0.355, 1)',

          '&::before': {
            top: 0,
            opacity: 0,
            transition: `top 0.1s ease-out,
                         opacity 0.1s 0.12s ease-out`,
          },

          '&::after': {
            bottom: 0,
            transform: 'rotate(-90deg)',
            transition: `bottom 0.1s ease-out,
                         transform 0.22s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1)`,
          },
        },
      },
    }),
  { name: 'Burger' },
);

export function Burger({ open, onOpenToggle }: Props) {
  const classes = useStyles();

  return (
    <IconButton
      className={clsx(classes.root, {
        [classes.open]: open,
      })}
      onClick={onOpenToggle}
      color="inherit"
    >
      <div className={classes.box}>
        <div className={classes.stick} />
      </div>
    </IconButton>
  );
}
