import React from 'react';
import {
  createStyles,
  Grid,
  makeStyles,
  Link as LinkComponent,
} from '@material-ui/core';
import { Link } from 'src/components';
import { Arrow } from 'src/icons';

type Action = {
  label: string;
  href: string;
};

type Props = {
  action: Action;
};

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      container: {
        paddingTop: 180,
        paddingBottom: 140,
      },

      arrow: {
        marginLeft: 75,
      },
    }),
  { name: 'FooterAction' },
);

export function FooterAction({ action }: Props) {
  const classes = useStyles();

  return (
    <Grid container justify="center" className={classes.container}>
      <Grid item>
        <Grid
          container
          alignItems="center"
          justify="center"
          component={Link}
          href={action.href}
        >
          <Grid item>
            <LinkComponent
              variant="h1"
              component="p"
              color="inherit"
              underline="always"
            >
              {action.label}
            </LinkComponent>
          </Grid>
          <Grid item>
            <Arrow className={classes.arrow} />
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  );
}
