export type ProjectSlug = typeof projectOrder[number];

export type Project<T extends ProjectSlug> = {
  title: string;
  category: 'ui/ux' | 'ux' | 'graphic';
  imageSrc: string;
  slug: T;
};

type ProjectsMap = { [P in ProjectSlug]: Project<P> };

export const projectsMap: ProjectsMap = {
  'delivery-app': {
    slug: 'delivery-app',
    title: 'ux prototype for a delivery app',
    category: 'ux',
    imageSrc: deliveryApp,
  },
  'parking-meter': {
    slug: 'parking-meter',
    title: 'parking meter (concept)',
    category: 'ui/ux',
    imageSrc: parkingMeter,
  },
  'tribe-socia': {
    slug: 'tribe-socia',
    title: 'tribe socia project',
    category: 'ui/ux' as const,
    imageSrc: tribeSocia,
  },
  'index-standard': {
    slug: 'index-standard',
    title: 'the index standard',
    category: 'ui/ux',
    imageSrc: indexStandard,
  },
  'net-worth-gains': {
    slug: 'net-worth-gains',
    title: 'net worth gains',
    category: 'ui/ux',
    imageSrc: netWorthGains,
  },
};

export const projects = projectOrder.map((slug) => projectsMap[slug]);
